import React, { useRef } from 'react';
import { useSpring, animated } from 'react-spring';
import AnimatedAngle from 'src/components/Animated/AnimatedAngle';
import { Theme } from '@mui/material';
import { makeStyles } from '@mui/styles';
import { useScrollDown } from 'src/hooks/useScrollDown';

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    position: 'relative',
    height: '100vh',
  },
  wrapper: {
    position: 'absolute',
    top: '45%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    [theme.breakpoints.down('md')]: {
      top: '30%',
    },
  },
  title: {
    fontStyle: 'italic',
    fontSize: '3.5rem',
    textAlign: 'center',
    whiteSpace: 'nowrap',
    [theme.breakpoints.down('md')]: {
      fontSize: '2rem',
    },
  },
  titlePoint: {
    color: theme.palette.secondary.main,
  },
  description: {
    textAlign: 'center',
    fontWeight: 200,
    fontSize: '1.4rem',
    fontStyle: 'italic',
  },
  bottomArrow: {
    position: 'absolute',
    bottom: '20%',
    left: '50%',
    transform: 'translateX(-50%)',
  },
}));

const MainJumbotron = () => {
  const containerRef = useRef<HTMLDivElement>(null);
  const styles = useStyles();

  const titleAnimation = useSpring({
    from: { opacity: 0, transform: 'translateX(-30px)' },
    to: { opacity: 1, transform: 'translateX(0)' },
  });
  const descriptionAnimation = useSpring({
    from: { opacity: 0, transform: 'translateX(30px)' },
    to: { opacity: 1, transform: 'translateX(0)' },
  });
  const bottomArrowAnimation = useSpring({
    from: { opacity: 0, transform: 'translateY(-30px)' },
    to: { opacity: 1, transform: 'translateY(0)' },
    delay: 300,
  });

  const scrollToBottom = useScrollDown(containerRef);

  return (
    <div ref={containerRef} className={styles.root}>
      <div className={styles.wrapper}>
        <animated.h1 style={titleAnimation} className={styles.title}>
          Growth
          <span className={styles.titlePoint}>.</span> And Share
        </animated.h1>
        <animated.h2 style={descriptionAnimation} className={styles.description}>
          This is <strong>Lubycon</strong>
        </animated.h2>
      </div>
      <div className={styles.bottomArrow}>
        <animated.div style={bottomArrowAnimation}>
          <AnimatedAngle onClick={scrollToBottom} />
        </animated.div>
      </div>
    </div>
  );
};

export default MainJumbotron;
