import React, { HTMLProps } from 'react';
import { Theme } from '@mui/material';
import { makeStyles } from '@mui/styles';

type Direction = 'left' | 'right' | 'down' | 'up';
const directionRotate: { [key in Direction]: string } = {
  left: '-45deg',
  right: '135deg',
  up: '45deg',
  down: '-135deg',
};

const useStyles = makeStyles<Theme, { size: number; direction: Direction }>(theme => ({
  icon: {
    display: 'inline-block',
    width: props => `${props.size}px`,
    height: props => `${props.size}px`,
    textIndent: '-9999px',
    borderTop: `2px solid ${theme.palette.text.primary}`,
    borderLeft: `2px solid ${theme.palette.text.primary}`,
    transition: 'all 0.3s ease-in-out',
    color: 'transparent',
    textDecoration: 'none',
    transform: props => `rotate(${directionRotate[props.direction]})`,
    cursor: 'pointer',
    '&:hover': {
      borderColor: theme.palette.text.primary,
      borderWidth: 5,
    },
    '&::before': {
      display: 'block',
      height: '200%',
      width: '200%',
      marginLeft: '50%',
      marginTop: '-50%',
      content: '""',
      transform: 'rotate(45deg)',
    },
  },
}));

interface Props extends HTMLProps<HTMLAnchorElement> {
  size?: number;
  direction?: Direction;
}
const AnimatedAngleIcon = ({ size = 40, direction = 'down', ...rest }: Props) => {
  const styles = useStyles({ size, direction });
  return <a className={styles.icon} {...rest} />;
};

export default AnimatedAngleIcon;
